import List from '../../../mixins/List.js'
export default {
	name: 'MyGoods',
	mixins: [List],
	data() {
		return {
			actions: [
				{
					title: '已售出',
					status: 2,
					isShow: true
				},
				{
					title: '删除',
					status: 5,
					isShow: true
				}
			]
		}
	},
	methods: {
		publish () {
			this.$push('/pages/goods/mine/publish-goods')
		}
	},
	methods: {
		getData () {
			this.$http({
				url: this.$urlPath.getMyGoodsList,
				params: {
					page: this.page.num
				},
				onRequestSuccess: (res) => {
					res.data.forEach(it => {
						it.goods_images = it.goods_images.split(',')
						it.goods_tags = it.goods_tags ? it.goods_tags.split(',') : ''
					})
					this.loadSuccess(res.data)
				},
				onRequestFail: (errorCode, error) => {
					this.$toast(error)
				}
			})
		},
		changeStatus (item, status) {
			this.$http({
				url: this.$urlPath.changeGoodsStatus,
				params: {
					gid: item.id,
					status: status
				},
				onRequestSuccess: (res) => {
					this.page.num = 1
					this.getData()
				},
				onRequestFail: (errorCode, error) => {
					this.$toast(error)
				}
			})
		},
		moreClick (item) {
			this.actions[0].isShow = item.status !== 2
			const temp = this.actions.filter(it => it.isShow)
			uni.showActionSheet({
				itemList: temp.map(it => it.title),
				success: (res) => {
					uni.showModal({
						content: '确定要关闭此信息吗',
						success: (r) => {
							if (r.confirm) {
								this.changeStatus(item, temp[res.tapIndex].status)
							}
						}
					})
				}
			})
		},
		goodsItemClick (item) {
			this.$push('/pages/goods/goods-info?gid=' + item.id)
		}
	},
	onLoad() {
		this.getData()
	}
}
